.App {
  font-family: Arial, sans-serif;
  padding: 20px;
}

.add-todo {
  margin-bottom: 20px;
}

table {
  width: 100%;
  border-collapse: collapse;
}

th, td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

th {
  background-color: #f2f2f2;
}

button {
  margin-left: 5px;
  padding: 5px 10px;
  cursor: pointer;
}

input {
  margin-right: 5px;
  padding: 5px;
}
